import {useContext} from 'react';
import { useSelector } from "react-redux";
import TaskOne from './TaskOne';
import TaskTwo from './TaskTwo';
import {TestContext} from '../TestContextProvider';
import SpeechContextProvider from "./speakingContext";

const Speaking = () => {
  const{ activePage } = useSelector(state => state.test);
  const {questions, setDefaultValues} = useContext(TestContext);

  const renderTask = () => {
    if(activePage === 'one'){
      return(
        <TaskOne 
        questions={questions.Speaking.task_1} 
        setDefaultValues={setDefaultValues}
        />
      )
    };

    if(activePage  === 'two'){
      return(
        <TaskTwo 
        questions={questions.Speaking.task_2} 
        setDefaultValues={setDefaultValues}
        />
      )
    };
  };

  return(
    <SpeechContextProvider>
      <div id="reading">
        {renderTask()}
      </div>
    </SpeechContextProvider>
  )
}
export default Speaking;