import {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import SectionHeader from "../children/utils/sectionHeader";
import useStateTemplate from "../children/utils/stateTemplate";
import WritingBox from "../children/question_blocks/writing/writingBox";
import {setAnswers,setStateTemplate} from '../../../redux/test/Actions';

const TaskTWO =({questions, setDefaultValues})=>{
  const dispatch = useDispatch();
  const{answers}=useSelector(state => state.test.answerObj)
  const{article,sections,totalQuestions} = questions;
  const[taskTwo, setTaskTwo] = useStateTemplate(totalQuestions);

  // Sets previous task answers saved in db
  useEffect(() =>{
    answers && 
    answers.writing_ans.task_2.length &&
    setTaskTwo(answers.writing_ans.task_2);
  },[answers.writing_ans.task_2]);

  // updates task asnwers for testLeft on every question attempt
  useEffect(()=>dispatch(setStateTemplate(taskTwo)), [taskTwo]);

  // Enables saving user's answers to db only once
  const saveOnBlur = () =>{
    answers.writing_ans.task_2 = taskTwo;
    dispatch(setAnswers(answers,true));

  };

  // Sets the values of the input tags that have been replaced with regex
  useEffect(()=>setDefaultValues(taskTwo));

  const setState =(question_number,value)=>{
    setTaskTwo(prevTaskTwo =>{
      prevTaskTwo[question_number - 1].answer = value;
      return [...prevTaskTwo]
    });
  }
  
  const useTaskTwo={
    // number: this is the question number to not include in the array
    // operator: either greater than or less than the number of questions to include
    // The above is because the writing is in 2 paragraphs
    getAnswers:(operator,number)=>{
      const filterByOperator= taskTwo.filter((question)=>{
        if(operator == "<"){
          return question.question_num < number
        }

        if(operator == ">="){
          return question.question_num >= number
        }
      });

      const filterByAnswer= filterByOperator.filter((question)=>question.answer.trim().length);

      return filterByAnswer
    }
  }

  const renderSections=()=>{
    return sections.map((section, index)=>{ 
      return(
        <section key={index.toString()}>

          <SectionHeader
          section={section}
          />

          <div className="selection-table selection-table-WR">
            <div>{section.table.heading}</div>
            {section.table.table_items_1.map((item, index)=>{
              return(
                <div key={index.toString()} className="tb-item">
                  <span>{item.option}</span>
                  <span>{item.value}</span>
                </div>
              )
            })}
            
            {section.table.table_items_2.map((item, index)=>{
              return(
                <div key={index.toString()} className="tb-item">
                  <span>{item.option}</span>
                  <span>{item.value}</span>
                </div>
              )
            })}
          </div>
          
          {section.question_blocks.map((block, index)=>{
            return(
              <div className="question-block" key={index.toString()}>
                <p className="block-header">
                  <strong>{block.heading}</strong>
                </p>
                <div className="inline-question">
                <strong style={{ marginRight:'10px'}}></strong>
                {index===0?(
                  <div className="writing-box-all" style={{ display:useTaskTwo.getAnswers("<",7).length? 'inline-block' :'none'}}>
                    {useTaskTwo.getAnswers("<",7).map((question,index)=><span key={index.toString()}>{question.answer}</span>)}
                  </div>
                ) : (<div className="writing-box-all" style={{ display:useTaskTwo.getAnswers(">=",7).length? 'inline-block' :'none'}}>
                    {useTaskTwo.getAnswers(">=",7).map((question,index)=><span key={index.toString()}>{question.answer}</span>)}
                    </div>
                )
                } 
                </div>
                {block.questions.map((question,index)=>{
                  if(taskTwo.length){
                    return(
                      <WritingBox
                      setState={setState}
                      saveOnBlur={saveOnBlur}
                      key={index.toString()}
                      question_number={question.question_number}
                      value={taskTwo[question.question_number-1].answer}
                      />
                    )
                  }
                })}
              </div>
            )
          })}
        </section>
      )
    })
  }
  return(
    <div id="writing-task-one">   
      <article id="readingPassage">
        {article.sentences.map((sentence,index)=>{
          return(
            <strong key={index.toString()}>
              {sentence}
            </strong>
          )
        })}
      </article>
      {renderSections()}
    </div> 
  )
};
export default TaskTWO;