import {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {AiFillAudio} from 'react-icons/ai';
import useStateTemplate from "../children/utils/stateTemplate";
import SingleAnswerMultipleChoice from "../children/question_blocks/multiple_choice/singleAnswer";
import SentenceCompletion from "../children/question_blocks/sentence_completion/sentenceCompletion";
import {setAnswers, setStateTemplate} from '../../../redux/test/Actions'

const TaskTwo = ({questions, setDefaultValues}) =>{

  const dispatch = useDispatch();
  const{article,sections,totalQuestions} = questions;
  const{answers} = useSelector(state => state.test.answerObj)
  const[taskTwo, setTaskTwo] = useStateTemplate(totalQuestions);

  // Sets previous task answers saved in db
  useEffect(() =>{
    answers && 
    answers.listening_ans.task_2.length &&
    setTaskTwo(answers.listening_ans.task_2)
  },[answers.listening_ans.task_2]);

  // updates task asnwers in redux on every question attempt
  useEffect(()=>dispatch(setStateTemplate(taskTwo)), [taskTwo]);

  // Enables saving user's answers to db only once
  const saveOnBlur = () =>{
    answers.listening_ans.task_2 = taskTwo;
    dispatch(setAnswers(answers,true));

  };
    
  // Sets the values of the input tags that have been replaced with regex
  useEffect(() =>setDefaultValues(taskTwo));

  const setState = (question_number,value)=>{
    setTaskTwo(prevTaskTwo =>{
      prevTaskTwo[question_number - 1].answer = value;
      return [...prevTaskTwo];
    })
  };

  const renderSections=()=>{
    return(
      sections.map((section, index)=>{
        
        if(section.type==="sentence_completion"){
          return(
            <SentenceCompletion
            section={section}
            setState={setState}
            saveOnBlur={saveOnBlur}
            key={index.toString()}
            />
          )
        };
    
        if(section.type==="multiple_choice"){
          return(
            <SingleAnswerMultipleChoice
            section={section}
            setState={setState}
            saveOnBlur={saveOnBlur}
            key={index.toString()}
            />
          )
        }
      })
    ) 
  }

  return(
    <div id="writing-task-one">  
      <article id="readingPassage">
        <div id="readingHeader">
          <div className="listening-audio audio-element-wrapper">
            <section>
            <AiFillAudio size={60}/>
            </section>
            <audio controls src={article.audio}/>
          </div>
        </div>
        <p className="sub-header">
          <strong>{article.title}</strong>
        </p>
      </article>
      {renderSections()}
    </div> 
  )
};
export default TaskTwo;