const WritingBox = ({question_number, value, setState, saveOnBlur}) =>{
  return(
    <p className="inline-question question-description">
      <strong>{question_number}</strong>
      <input 
        className="writing-box"
        // id={question_number} 
        type="text" 
        placeholder="type sentence..." 
        value={value || ""}
        onChange={(e)=>setState(question_number,e.target.value)}
        onBlur={saveOnBlur}
      />
    </p>
  )
}
 
export default WritingBox;