import {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import SectionHeader from "../children/utils/sectionHeader";
import SelectionTable from "../children/utils/selectionTable";
import useStateTemplate from "../children/utils/stateTemplate";
import WritingBox from "../children/question_blocks/writing/writingBox";
import {setAnswers, setStateTemplate} from '../../../redux/test/Actions'

const TaskOne = ({questions, setDefaultValues}) => {
  const dispatch = useDispatch();
  const{ answers }=useSelector(state => state.test.answerObj)
  const{article,sections,totalQuestions} = questions;
  const[taskOne, setTaskOne] = useStateTemplate(totalQuestions);

  // Sets previous task answers saved in db
  useEffect(() => {
    answers && 
    answers.writing_ans.task_1.length &&
    setTaskOne(answers.writing_ans.task_1)
  },[answers.writing_ans.task_1]);


  // updates task asnwers in redux on every question attempt
  useEffect(()=>dispatch(setStateTemplate(taskOne)),[taskOne]);

  // Enables saving user's answers to db only once
  const saveOnBlur = () =>{
    answers.writing_ans.task_1 = taskOne
    dispatch(setAnswers(answers,true));
  }

  // Sets the values of the input tags that have been replaced with regex: MAKE SURE TO GIVE TAGS to be REPLACED AN ID
  useEffect(()=>setDefaultValues(taskOne) );

  // Saves in-realTime for left comp and writing box editing
  const setState =(question_number,value)=>{
    setTaskOne(prevTaskOne =>{
      prevTaskOne[question_number - 1].answer = value;
      return [...prevTaskOne];
    });

  }

  const useTaskOne ={
    getAnswers:()=>taskOne.filter((question)=>question.answer)
  };

  const renderSections=()=>{
   return sections.map((section, index)=>{
    return(
      <section key={index.toString()}>

        <SectionHeader
        section={section}
        />

        <SelectionTable
        section={section}
        />
        
        <div className="question-block">
        <div className="inline-question">
          <strong style={{ marginRight:'10px'}}></strong>
          <div className="writing-box-all" style={{ display:useTaskOne.getAnswers().length? 'inline-block' :'none'}}>
            {useTaskOne.getAnswers().map((question,index)=>{
              return(
                <span key={index.toString()}>
                  {question.answer}
                </span>
              )
            })}
          </div>
        </div>
        
        {section.question_blocks.map(({question_number})=>{
          if(taskOne.length){
            return(
              <WritingBox
              setState={setState}
              saveOnBlur={saveOnBlur}
              key={question_number.toString()}
              question_number={question_number}
              value={taskOne[question_number-1].answer}
              />
            )
          }
        })}
      </div>
      </section>
    )
  })
  }
  return (
    <div id="writing-task-one">   
      <article id="readingPassage">
        <div id="readingHeader">
          <h1>{article.title}</h1>
          <img alt="Sponsorfy reading" src={article.img}/>
        </div>
      </article>
      {renderSections()}
    </div> 
  )
};
export default TaskOne;